import './devbx.form.layout.field.text.css';
import fieldMixin from "./../field.mixin";

BX.addCustomEvent("DevBxWebFormCreated", function(app) {
    app.component('devbx-form-layout-field-text', {
        mixins: [fieldMixin],
        data() {
            return {
                inputTimer: false
            }
        },
        watch: {
            'defaultValue': {
                immediate: true,
                handler: function (val) {
                    if (!this.field.changed) {
                        this.field.value = val;
                    }
                }
            },
        },
        methods: {
            onInput(event) {
                let value = event.target.value;

                if (this.maximumLength) {
                    value = value.substring(0, this.maximumLength);
                    event.target.value = value;
                }

                let self = this;

                if (this.inputTimer)
                    clearTimeout(this.inputTimer);

                this.inputTimer = setTimeout(function () {

                    self.inputTimer = false;
                    self.onChange();

                }, 300);
            },

            onChange() {
                if (!this.$refs.input)
                    return;

                let value = this.$refs.input.value.trim();

                if (this.maximumLength) {
                    value = value.substring(0, this.maximumLength);
                }

                this.$refs.input.value = value;
                this.field.value = value;
                this.field.changed = true;

                this.checkErrors();
            },

            checkErrors() {
                if (this.checkRuleErrors())
                    return true;

                if (this.minimumLength) {
                    if (this.minimumLength > this.field.value.length) {
                        this.errorMessage = this.$root.formatString(
                            this.$Bitrix.Loc.getMessage('DEVBX_WEB_FORM_ERR_FIELD_REQUIRED_MIN_LENGTH'),
                            ['#FIELD_NAME#', '#MIN_LENGTH#'],
                            [this.labelFormatted, this.minimumLength]);
                        return true;
                    }
                }

                return false;
            },

            setFocus() {
                this.$refs.input.focus();
            },
        },
        computed: {
            defaultValue() {
                return this.config.defaultValue;
            },

            minimumLength() {
                let value = parseInt(this.config.lengthMin);

                if (isNaN(value) || value <= 0)
                    return false;

                return value;
            },

            maximumLength() {
                let value = parseInt(this.config.lengthMax);

                if (isNaN(value) || value <= 0)
                    return false;

                return value;
            },

            field() {
                return this.$root.formFieldByName['Fields.' + this.config.fieldName];
            },

            value: {
                get() {
                    return this.field.value;
                },

                set(value) {
                    this.field.value = value.trim();
                    this.field.changed = true;
                },
            },
        },
        template: `
    <div class="devbx-webform-field" :class="{'devbx-webform-required': required, 'devbx-webform--is-error': errorMessage.length}">
        <label class="devbx-webform-label" :for="id" v-html="formLabelFormatted" v-if="!config.labelHidden"></label>
        <div class="devbx-webform-field-input" v-if="config.type == 'SINGLE_LINE'">
            <input ref="input"
                autocomplete="off" 
                class="devbx-webform-input" 
                :id="id" 
                type="text" 
                :value="value"
                :placeholder="config.placeholder" 
                :readonly="readonly"
                @input="onInput"
                @change="onChange"
                >
        </div>
        <div class="devbx-webform-field-input" v-if="config.type == 'MULTI_LINE'">
            <textarea ref="input" 
                autocomplete="off" 
                class="devbx-webform-textarea" 
                style="resize:both;"
                :id="id" 
                :value="value"
                :placeholder="config.placeholder" 
                :readonly="readonly"
                @input="onInput"
                @change="onChange"
                >
            </textarea>
        </div>
        
        <div class="devbx-webform-helptext" v-if="helpTextFormatted.length>0" v-html="helpTextFormatted"></div>

        <transition name="devbx-webform-field">
            <div class="devbx-webform-error-message" v-if="errorMessage" v-html="errorMessage"></div>
        </transition>
    </div>
    `
    });
});